import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../../actions/LogicAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { getListPacking } from "../../actions/validasiPOAction";
import { gagal, warning } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class FormPackingCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_customer",
          text: "Kode Customer",
        },
        {
          dataField: "no_ext_packing",
          text: "Nomor Packing",
        },
        {
          dataField: "no_po",
          text: "Nomor PO",
        },
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "image",
          text: "Image",
          formatter: (cell, row, rowIndex, extraData) => (
            <div>
              <img
                src={row.image}
                alt="Gambar Packing"
                width="150px"
                height="150px"
              />
            </div>
          ),
        },

        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.getBarang(row.no_ext_packing)}
                >
                  Pilih
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListPacking());
  }

  async getBarang(data) {
    localStorage.setItem("packing_nomor", data);
    let hasil = [];
    await AxiosMasterGet(`packing-list/get-packing/${data}`)
      .then((res) => (hasil = res.data[0]))
      .catch((err) =>
        gagal(`Gagal Ambil Packing, Detial : ${err.response.data}`)
      );
    this.pilihBarcode(hasil);
  }
  async pilihBarcode(data) {
    let detail_barang = [];
    let packingM = data.packing_m;
    packingM.map((packingM) => {
      packingM.packing_s.map((packingS) => {
        detail_barang.push(...packingS.detail_barang);
        return true;
      });
      return true;
    });
    let sebelum = JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
    let hasil = detail_barang;
    var GrandTotalNetto = hasil
      .map((list) => parseFloat(list.netto_disc))
      .reduce((a, b) => a + b, 0);

    hasil.forEach((data, index) => {
      delete data._id;
      delete data.kode_kategori;
      delete data.nama_jenis;
      let posisi = sebelum.findIndex((list) => list.netto === data.netto);
      if (posisi < 0) {
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(hasil));
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormTransaksiPenjualan"));

        this.props.dispatch(getNetto("transaksiPenjualan"));
        this.setState({
          discGlobalGr:
            (parseFloat(0) / 100) *
            parseFloat(localStorage.getItem("GrandTotalNetto")),
          discGlobalPr: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      } else {
        warning("Barang Sudah Ditambahka di List barang");
      }
    });
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <GlobalTabel
          keyField="kodeJenis"
          data={this.props.listPacking}
          columns={this.state.columns}
          textEmpty="Data Timbangan Kosong"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listPacking: state.provinsi.listPacking,
  };
})(FormPackingCustomer);
